.testimonial{
	background-color: rgba(255,255,255, .8);
	padding: 0 18px 1px 18px;
	margin-bottom: 80px;

	@include atMedium(){
		margin-bottom: 0px;
		padding: 0 1.1825rem 1px 1.1825rem;
	}

	&--last {
		margin-bottom: 0;
	}

	&__photo{
		border-radius: 80px;
		overflow: hidden;
		width: 160px;
		height: 160px;
		margin: 0 auto -80px auto;
		position: relative;
		top: -80px; 
		border: 6px solid rgba(255,255,255, .8);
	}

	&__title{
		color: $mainBlue;
		text-align: center;
		font-size: 1.5rem;
		font-weight: 500;
		margin: 0;
	}

	&__subtitle{
		color: $mainOrange;
		text-align: center;
		font-weight: 400;
		font-size: .9375rem;
		margin: .25rem 0 1rem 0;

	}
}